import { createSelector } from '@ngrx/store';
import { EntityState, EntitySelectors, Dictionary } from './models';
/**
 * @template T
 * @return {?}
 */
export function createSelectorsFactory<T>() {
  function getSelectors(): EntitySelectors<T, EntityState<T>>;
  function getSelectors<V>(
    selectState: (state: V) => EntityState<T>
  ): EntitySelectors<T, V>;
/**
 * @param {?=} selectState
 * @return {?}
 */
function getSelectors(
    selectState?: (state: any) => EntityState<T>
  ): EntitySelectors<T, any> {
    const /** @type {?} */ selectIds = (state: any) => state.ids;
    const /** @type {?} */ selectEntities = (state: EntityState<T>) => state.entities;
    const /** @type {?} */ selectAll = createSelector(
      selectIds,
      selectEntities,
      (ids: T[], entities: Dictionary<T>): any =>
        ids.map((id: any) => ( /** @type {?} */((entities as any)))[id])
    );

    const /** @type {?} */ selectTotal = createSelector(selectIds, ids => ids.length);

    if (!selectState) {
      return {
        selectIds,
        selectEntities,
        selectAll,
        selectTotal,
      };
    }

    return {
      selectIds: createSelector(selectState, selectIds),
      selectEntities: createSelector(selectState, selectEntities),
      selectAll: createSelector(selectState, selectAll),
      selectTotal: createSelector(selectState, selectTotal),
    };
  }

  return { getSelectors };
}
